        },
        // 编辑负责人信息
        selectUserRow: {},
        schoolUserFormEdit:{
          name: '',
          phoneNumber: '',
        },
        schoolUserAddOrEditRules: {
          name: [
            {
              required: true,
              message: '请输入姓名',
              trigger: 'blur'
            },
            {
              pattern: /^[\u4E00-\u9FA5]+$/,
              message: '用户名只能为中文',
              trigger: 'blur'
            },
            {
              min: 2,
              max: 10,
              message: '长度在 2 到 10 个汉字',
              trigger: 'blur'
            }
          ],
          phoneNumber: [
            {
              required: true,
              message: '请输入手机号',
              trigger: 'blur'
            },{
              pattern: /^1[34578]\d{9}$/,
              message: '请输入11位手机号',
              trigger: 'blur'
            }
          ],
        },
        value:'',
        oldSchoolInfo: {

