        schoolInfo:{
          name:'',
          schoolType: '',  //学校类型
          codeType: '',    //学制
          region: '', //教学范围
          position: '',   //地理位置
          address:'',
          teacherCount: '',
          studentCount: '',
          codeStart: '',   //教学开始
          codeEnd: '',      //教学结束
          note:'',
        },
        //选中教学范围
        selectedGrad: [],
