      this.thisSchool.type = this.$route.params.type;
/*
      this.thisSchool.id = this.getQueryString('id');
      this.thisSchool.type = this.getQueryString('type');
*/
      //获取当前学校基本信息
      this.getSchoolDetail();
      //编辑状态下，读取所在地址三级联动。
      if(!this.isEmptyData(this.thisSchool.type) && this.thisSchool.type  === 'edit'){
        this.getAreaTree();
      }
      //获取当前学校负责人列表
      this.getSchoolUsers();
    },
    methods: {
    	//获取地址栏
    	getQueryString(name){
        return decodeURIComponent((new RegExp('[?|&]'+name+'='+'([^&;]+?)(&|#|;|$)').exec(location.search)||[,''])[1].replace(/\+/g,'%20'))||null;
      },
      //判断你是否空对象
      isEmptyData(name){
    		return name !== undefined && name !== null && name !== '' ? false : true;
      },
      //获取当前学校信息
      getSchoolDetail(){
        let schoolDetailPath = '/schadmin/getOneSchool?id='+this.thisSchool.id;
        if(!this.isEmptyData(this.thisSchool.type) && this.thisSchool.type  === 'edit'){
          schoolDetailPath = '/eduadmin/updateOneSchoolShow?id='+this.thisSchool.id;
          this.isEditing = true;
        }else{
          this.isEditing = false;
        }
        this.$ajax(schoolDetailPath)
          .then(res => {
            this.schoolInfo = res.data; //学校信息
            this.oldSchoolInfo = res.data; //真实学校信息
            this.fullscreenLoading = false;
            this.initSelectGrad();
          });
      },
      getSchoolUsers(){
        let schoolUsersPath = '/eduadmin/updateOneSchoolShow?id='+this.thisSchool.id;
        this.$ajax(schoolUsersPath)
          .then(res => {
            this.schoolUsers = res.data.fuserList || [];
          });
      },
      //选取当前读取到的教学范围
      initSelectGrad(){
      	  let codeType = this.schoolInfo.codeType;
          //控制呈现年级
          if(codeType === '6-3'){
          	this.codeTypeVal = 0;
            this.grads[5].display = true; //如果是六三制，展示6年级
            this.grads[9].display = false;//如果是六三制，不展示初四；
          }
          else{
          	this.codeTypeVal = 1;
            this.grads[5].display = false;
            this.grads[9].display = true;
          }
          this.computeCodeRule();
          this.initGradByCodeType(this);
      },
