      editShowClick(){
        this.isEditing = true;
      },
      //取消学校信息保存
      cancelSaveClick(){
        this.isEditing = false;
        this.schoolInfo = this.oldSchoolInfo;
      },
      //保存学校信息
      saveSchoolInfo(){
        console.log(this.schoolInfo);
        let saveObj = {
          name: this.schoolInfo.name,
          schoolType:  this.schoolInfo.schoolType,  //学校类型
          codeType: this.schoolInfo.codeType,    //学制
          region: this.schoolInfo.region,
          position: this.schoolInfo.position,   //地理位置
          address: this.schoolInfo.address,
          teacherCount: this.schoolInfo.teacherCount,
          studentCount: this.schoolInfo.studentCount,
          codeStart: this.schoolInfo.codeStart,   //教学开始
          codeEnd: this.schoolInfo.codeEnd,      //教学结束
          note: this.schoolInfo.note,
        }
        console.log(saveObj);
      },
      //新建负责人信息
      showAddDialog(){
      	this.schoolUserFormAdd = {};
        this.dialogVisibleAdd = true;
      },
      addSchoolUser(){
        this.$refs.schoolUserFormAdd.validate((valid) => {
          if (valid) {
            let params={
              name: this.schoolUserFormAdd.name,
              phoneNumber: this.schoolUserFormAdd.phoneNumber,
              schoolId: this.schoolInfo.id
            };
            this.$ajax('/eduadmin/saveSchoolAdmin',params)
              .then(res => {
                if(res.code == 200){
                  this.$message({ type: 'success', message: '添加成功!'});
                  this.dialogVisibleAdd = false;
                  this.getSchoolUsers();
                }else{
                  this.$message({type: 'error',message: res.message});
                  this.dialogVisibleAdd = false;
                }
              });
          }
          else {
            return false;
          }
        }, this);
      },
      //编辑负责人信息
      showEditDialog(index, row){
        this.schoolUserFormEdit.name = row.name;
        this.schoolUserFormEdit.phoneNumber = row.phoneNumber;
        this.dialogVisibleEdit = true;
        this.selectUserRow = row;
      },
      editSchoolUser(){
        this.$refs.schoolUserFormEdit.validate((valid) => {
          if (valid) {
            let params = {
              name: this.schoolUserFormEdit.name,
              phoneNumber: this.schoolUserFormEdit.phoneNumber,
              schoolId: this.schoolInfo.id,
              userId: this.selectUserRow.userId,
              id: this.selectUserRow.id
            };
            console.log(params);
            this.$ajax('/eduadmin/updateSchoolAdmin',params)
              .then(res => {
                if(res.code == 200){
                  this.$message({ type: 'success', message: '修改成功!'});
                  this.dialogVisibleEdit = false;
                  this.getSchoolUsers();
                }
                else{
                  this.$message({type: 'error',message: res.message});
                  this.dialogVisibleEdit = false;
                }
              });
          }
          else {
            return false;
          }
        }, this);
