          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(() => {
          this.fullscreenLoading = true;
          let params = {
            phoneNumber: row.phoneNumber
          };
          this.$ajax('/password/reset',params).then((res) => {
            this.fullscreenLoading = false;
            if(res.code == 200){
              this.$message({message: '重置成功',type: 'success'});
            }
          });
        }).catch(() => {
        });
