      changeSchoolUserStatus(command){
        let params={
          userId: this.currentInfo.id,
          state: command
        };
        this.$message({
          message: '状态修改功能后台待开发',
          type: 'success'
        });
        /*this.$ajax('/schadmin/changeState',params).then((res) => {
          if(res.code == 200){
            this.$message({
              message: '状态修改成功',
              type: 'success'
            });
            this.getSchoolUsers()
          }
        }).catch(() => {
        });*/
      },
      //删除负责人信息
      delSchoolUser(index, row){
        this.$confirm('确定要删除选中负责人吗?', '提示', {
          type: 'warning',
          confirmButtonText: '删除',
          cancelButtonText: '取消',
        }).then(() => {
          let params = {
              userId: row.userId,
              schoolId: this.schoolInfo.id
          };
          this.$ajax('/eduadmin/deleteSchoolAdmin',params).then((res) => {
            if(res.code == 200){
              this.$message({ message :'删除成功',type: 'success'});
              this.getSchoolUsers();
            }else{
              this.$message({type: 'error',message: res.message});
            }
          });
        }).catch(() => {
        });
      },
      //批量删除负责人信息
      delMoreSchoolUser(index, row){
        this.$message({
          message: '批量删除接口联调未成功',
          type: 'success'
        });
        /*let teacherIds = this.multipleSelection.map(item => item.id).toString();
        this.$confirm('确定要删除选中负责人吗?', '提示', {
          type: 'warning',
          confirmButtonText: '删除',
          cancelButtonText: '取消',
        }).then(() => {
          let params = {
            teacherIds: teacherIds,
            schoolId: this.schoolInfo.id
          };
          this.$ajax('/eduadmin/deleteManySchoolAdmin',params).then((res) => {
            if(res.code == 200){
              this.$message({
                message: '删除成功',
                type: 'success'
              });
              this.getSchoolUsers();
            }else{
              this.$message({type: 'error',message: res.message});
            }
          });
        }).catch(() => {
        });*/
      },
      //下面三个是教学范围联动
      initGradByCodeType(){
      	//6-3制度  小1-6 初7-9 高10-12
        //5-4制度  小1-5 初6-9 高10-12
        for(let i = 0;i< this.grads.length;i++) {
        	//设定给定值选中未选中问题[因为从1开始的，所以选择的时候下标往前推一个]
          if(i >= (this.schoolInfo.codeStart-1) && i <= (this.schoolInfo.codeEnd-1) ){
            this.grads[i].checked = true;
            this.grads[i].disabled = false;
            this.selectedGrad.push(i);
            this.codeTypes.push(this.grads[i].name);
          }else{
          	this.grads[i].disabled = true;
            this.grads[i].checked = false;
          }
        }
      },
      changeSchoolType() {
        //设定学制范围：
        if(!this.schoolInfo.schoolType || this.schoolInfo.schoolType<3  || this.schoolInfo.schoolType>8){
          this.schoolInfo.codeType = '';
        }else{
        	if(this.isEmptyData(this.schoolInfo.codeType) && this.schoolInfo.schoolType>=3 && this.schoolInfo.schoolType<=8){
        		this.schoolInfo.codeType = '6-3';
          }
        }
        this.changeSchooling();
      },
      computeCodeRule(codeType){
      	let schoolType = this.schoolInfo.schoolType;
      	//1:大学2:高中3:初中4:完中5:九年一贯制6:小学7:教学点8:完小9:职业高中(职高)10：中等专业学校(中专)11:技工学校(技校)12:普通高等学校(体校、军校)
      	if(schoolType == 6 || schoolType == 8){
      		//小学、完小
          if(codeType === '6-3'){
            this.selectEndCode = 5;
            this.selectStartCode = 0;
          }else{
            this.selectEndCode = 4;
            this.selectStartCode = 0;
          }
        }
        else if(schoolType == 3){
      		//初中
          if(codeType === '6-3'){
            this.selectEndCode = 8;
            this.selectStartCode = 6;
          }else{
            this.selectEndCode = 9;
            this.selectStartCode = 6;
          }
        }
        else if(schoolType == 5){
          //九年一贯制
          this.selectEndCode = 9;
          this.selectStartCode = 0;
        }
        else if(schoolType == 2 || schoolType == 9){
        	//高中、职高
          this.selectEndCode = 12;
          this.selectStartCode = 10
        }
        else if(schoolType == 4){
          //完中【其中9是不呈现的】
          this.selectEndCode = 12;
          this.selectStartCode = 6
        }
        else{
          //职高
          this.selectEndCode = 0;
          this.selectStartCode = 12
        }
      },
      resetGradByCodeType(thisParams){
        let tempIndexStart =  -1;
        let tempIndexEnd = -1;
        //1:大学2:高中3:初中4:完中5:九年一贯制6:小学7:教学点8:完小9:职业高中(职高)10：中等专业学校(中专)11:技工学校(技校)12:普通高等学校(体校、军校)
        //小学5-4，默认1-5年级
        //小学6-3，默认1-6年级
        //完小 同 小学
        if(thisParams.schoolInfo.schoolType == 6 || thisParams.schoolInfo.schoolType == 8){
        	if(this.schoolInfo.codeType === '5-4'){
            thisParams.schoolInfo.codeEnd = 5;
            thisParams.schoolInfo.codeStart = 1;
            tempIndexStart = 0;
            tempIndexEnd = 4;
          }else if(this.schoolInfo.codeType === '6-3'){
            thisParams.schoolInfo.codeEnd = 6;
            thisParams.schoolInfo.codeStart = 1;
            tempIndexStart = 0;
            tempIndexEnd = 5;
          }
        }
        //初中5-4，默认初1-4
        //初中6-3，默认初1-3
        else if(thisParams.schoolInfo.schoolType == 3) {
        	if(thisParams.schoolInfo.codeType == '6-3'){
            thisParams.schoolInfo.codeStart = 7;
            thisParams.schoolInfo.codeEnd = 9;
            tempIndexStart = 6;
            tempIndexEnd = 8;
          }else if(this.schoolInfo.codeType === '6-3'){
            thisParams.schoolInfo.codeStart = 6;
            thisParams.schoolInfo.codeEnd = 9;
            tempIndexStart = 6;
            tempIndexEnd = 9;
          }
        }
        //九年一贯制5-4，默认1-5年级+初1-4
        //九年一贯制6-3，默认1-6年级+初1-3
        else if(thisParams.schoolInfo.schoolType == 5){
          if(thisParams.schoolInfo.codeType != '') {
            thisParams.schoolInfo.codeStart = 1;
            thisParams.schoolInfo.codeEnd = 9;
            tempIndexStart = 0;
            tempIndexEnd = 9;
          }
        }
        //完中5-4，默认初1-初4,+高1-3
        //完中6-3，默认初1-初3,+高1-3
        else if(thisParams.schoolInfo.schoolType == 4){
        	if(this.schoolInfo.codeType === '5-4'){
            thisParams.schoolInfo.codeStart = 6;
            thisParams.schoolInfo.codeEnd = 12;
            tempIndexStart = 6;
            tempIndexEnd = 12;
          }else if(this.schoolInfo.codeType === '6-3'){
            thisParams.schoolInfo.codeStart = 7;
            thisParams.schoolInfo.codeEnd = 12;
            tempIndexStart = 6;
            tempIndexEnd = 12;
          }
        }
        //高中/职高/中专，默认高1-3
        else if(thisParams.schoolInfo.schoolType == 2 || thisParams.schoolInfo.schoolType == 9 || thisParams.schoolInfo.schoolType == 10){
          if(thisParams.schoolInfo.codeType != '') {
            thisParams.schoolInfo.codeStart = 10;
            thisParams.schoolInfo.codeEnd = 12;
            tempIndexStart = 10;
            tempIndexEnd = 12;
          }
        }
        //默认选中
        thisParams.selectedGrad = [];
        thisParams.codeTypes = [];
        if(tempIndexStart !== -1 && tempIndexEnd !== -1){
          for(let i = 0;i< thisParams.grads.length;i++) {
            if(i >= tempIndexStart && i <= tempIndexEnd ){
              thisParams.grads[i].checked = true;
              thisParams.grads[i].disabled = false;
              thisParams.selectedGrad.push(i);
              thisParams.codeTypes.push(thisParams.grads[i].name);
            }else{
              thisParams.grads[i].checked = false;
              thisParams.grads[i].disabled = true;
            }
          }
        }
      },
      changeSchooling() {
      	let codeType = this.schoolInfo.codeType || '';
      	//重置选择信息
        let grads = grads_default;
        this.selectedGrad = [];
        this.codeTypes = [];
        //控制呈现年级
        if(codeType === '6-3'){
          grads[5].display = true; //如果是六三制，展示6年级
          grads[9].display = false;//如果是六三制，不展示初四；
        }else{
          grads[5].display = false;
          grads[9].display = true;
        }
        this.grads = grads;
        this.schoolInfo.codeStart = -1;
        this.schoolInfo.codeEnd = -1;
        //初始默认设置
        this.computeCodeRule();
        this.resetGradByCodeType(this);
      },
      changeGrad(row) {
      	console.log(row);
      },
      //下面四个是省市区地址联动
      getAreaTree(){
        this.$ajax('/org/getTree')
          .then(res => {
            this.province = res.data;
          }).catch(() => {

        });
      },
      provinceChange(id){
        this.selectedProvince = id;
        let params={
          parentId: this.selectedProvince
        };
        this.$ajax('/org/getChildren',params)
          .then(res => {
            this.city = res.data;
          }).catch(() => {
        });
      },
      cityChange(id){
        this.selectedCity = id;
        let params = {
          parentId: this.selectedCity
        };
        this.$ajax('/org/getChildren',params)
          .then(res => {
            this.county = res.data;
          }).catch(() => {
        });
      },
      countyChange(id){
        this.selectedCounty = id;
      },
      //设定当前点击行信息
      getThisRowInfo(row){
        this.currentInfo = row;
      },
      //获取列表选中列
      getSelectedUsersId(val) {
        this.multipleSelection = val;
      }
    },
    created: function() {
      this.grads = JSON.parse(JSON.stringify(grads_default));
    },
    watch: {
      searchText(val) {
        this.$refs.orgLists.filter(val);
      }
    },
  }
</script>

